import React from 'react';
import BugForm from './BugForm.js'
import BugList from './BugList.js'
import { Container, Grid } from '@material-ui/core';

class Bug extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            isEditBug: false
        }
    }

    render() {
        return (
            <React.Fragment>                
                <Container>
                    <Grid container spacing={2}>
                        <Grid item xs={12} md={4} lg={5}>
                            <BugForm isEdit={this.state.isEditBug} cancelEdit ={ () => this.setState({isEditBug: false }) }  showLoading = { (val) => this.props.showLoading(val) } />
                        </Grid>
                        <Grid item xs={12} md={8} lg={7}>
                            <BugList editBug={ (val) => this.setState({ isEditBug: val}) } />
                        </Grid>
                    </Grid>                
                </Container>
            </React.Fragment>
        )
    }
}

export default Bug;