import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getBugs, getBug, deleteBug } from '../../store/modules/bugs/actions.js'
import { SET_BUGS_PAGE_NUMBER, RESET_BUG } from '../../store/modules/bugs/types.js'
import { TableContainer, Paper, Table, TableHead, TableBody, TableFooter, TableRow, TableCell, IconButton, TablePagination  } from '@material-ui/core';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import { useConfirm  } from 'material-ui-confirm';

export default function BugList(props) {

    const dispatch = useDispatch()

    const confirm = useConfirm();
    
    const { bugList, pageSize, pageNumber, totalCount, BugStatus: bugStatusList } = useSelector(state => state.bugReducer)
    
    const [columnHeaders, setColumnHeaders] = useState([])    

    useEffect(() => {
        setColumnHeaders([
            "#",
            "Title",
            "Status",
            "Severity",
            "Created On",
            "Actions"
        ])
        dispatch(getBugs())
    }, [])   

    const handleEditClick = (event) => {
        const bugId = event.currentTarget.value;
        props.editBug(true);
        dispatch(getBug(bugId))
    }

    const handleDeleteClick = (event) => {        
        const bugId = event.currentTarget.value;
        confirm().then(() => {
            dispatch(deleteBug(bugId))
        });

        props.editBug(false);
        dispatch({type: RESET_BUG})
    }

    const handleChangePage = (event, newPage) => {
        dispatch({type: SET_BUGS_PAGE_NUMBER, payload: newPage})
        dispatch(getBugs())
    }

    return(
        <div>
            <TableContainer>
                <Table>
                    <TableHead>
                        <TableRow>
                            {columnHeaders.map(header => <TableCell key={header}>{header}</TableCell>)}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {bugList.map(function(bug, index) {
                            return(
                                <TableRow key={bug.bugId}>
                                    <TableCell><SerialNumber index={index} pageSize={pageSize} pageNumber={pageNumber} /></TableCell>
                                    <TableCell><div class="text-block" title={bug.title}>{bug.title}</div></TableCell>
                                    <TableCell><BugStatus bugStatusList={bugStatusList} bugStatusId={bug.bugStatusId}></BugStatus></TableCell>
                                    <TableCell>{bug.bugSeverityId}</TableCell>
                                    <TableCell><CreatedDate>{bug.createdOn}</CreatedDate></TableCell>
                                    <TableCell>
                                        <div>
                                            <IconButton onClick={handleEditClick} value={bug.bugId} size="small" color="primary"><EditIcon>Edit</EditIcon></IconButton>
                                            <IconButton onClick={handleDeleteClick} value={bug.bugId} size="small" color="secondary"><DeleteIcon>Delete</DeleteIcon></IconButton>                                            
                                        </div>
                                    </TableCell>
                                </TableRow>
                            )
                        })}
                    </TableBody>
                    <TableFooter>
                        <TableRow>
                            <TablePagination rowsPerPageOptions={[]} rowsPerPage={pageSize} page={pageNumber} count={totalCount} onChangePage={handleChangePage}></TablePagination>
                        </TableRow>
                    </TableFooter>
                </Table>
            </TableContainer>
        </div>)
}

function SerialNumber(props) {
    const number = props.index + 1;
    const pageSize = props.pageSize;
    const pageNumber = props.pageNumber + 1;

    const serialNumber = ((pageSize * pageNumber) - pageSize) + number;

    return (<span>{serialNumber}</span>)
}

function BugStatus(props) {
    const bugStatus = props.bugStatusList.find(bugStatus => bugStatus.id === props.bugStatusId);

    if(bugStatus)
        return (bugStatus.label)

    return null
}

function CreatedDate(props) {
    const date = new Date(props.children);
    return (<span>{date.getDate()}-{date.getMonth()}-{date.getFullYear()}</span>)
}