import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { createBug, updateBug } from '../../store/modules/bugs/actions.js'
import { RESET_BUG, SET_BUG, REMOVE_EXISTING_BUG_FILE } from  '../../store/modules/bugs/types.js'
import { FormControl, TextField, InputLabel, Select, MenuItem, Input, Button, GridList, GridListTile, GridListTileBar, IconButton } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import Notification from '../widgets/notification.js';

export default function BugForm (props) {

    const bug = useSelector(state => state.bugReducer.bug)
    const isReset = useSelector(state => state.bugReducer.isReset)
    const bugSoftwareTypes = useSelector(state => state.bugReducer.BugSoftwareTypes)
    
    const [title, setTitle] = useState('')
    const [recreationSteps, setRecreationSteps] = useState('')
    const [aboutSoftware, setAboutSoftware] = useState('')
    const [bugSoftwareTypeId, setBugSoftwareTypeId] = useState(0)
    const [comment, setComment] = useState('')
    const [formFiles, setFormFiles] = useState([]) 

    useEffect(() => {
        setTitle(bug.title)
        setRecreationSteps(bug.recreationSteps)
        setAboutSoftware(bug.aboutSoftware)
        setBugSoftwareTypeId(bug.bugSoftwareTypeId)
        setComment(bug.comment)        
    }, [bug])

    useEffect(() => {
        setFileInputKey(fileInputKey === 0 ? 1 : 0);
        setFormFiles([]);
        props.cancelEdit();
    }, [isReset])

    useEffect(() => {
        setFileInputKey(fileInputKey === 0 ? 1 : 0);
        setFormFiles([]);
    }, [props.isEdit])

    const dispatch = useDispatch()

    const [fileInputKey, setFileInputKey] = useState(0)

    const handleSubmit = async (event) => {
        event.preventDefault();
        dispatch({ type: SET_BUG, payload: { title, recreationSteps, aboutSoftware, bugSoftwareTypeId, comment } })
        props.showLoading(true)
        if(props.isEdit)
            await dispatch(updateBug(formFiles))
        else            
            await dispatch(createBug(formFiles))

        props.showLoading(false)
    }

    const handleCancelEdit = () => {
        dispatch({ type: RESET_BUG })
        setFileInputKey(fileInputKey === 0 ? 1 : 0);
        props.cancelEdit();
    }

    let button;
    if(props.isEdit){
        button = <div>
                    <Button onClick={handleCancelEdit} varaint="contained">Cancel</Button> 
                    <Button onClick={handleSubmit} type="submit" variant="contained" color="primary">Update</Button>
                </div>
    }
    else{
        button = <div><Button onClick={handleSubmit} variant="contained" color="primary">Create</Button></div>
    }

    return (
        <React.Fragment>
            <form autoComplete="off" noValidate>
                <FormControl fullWidth={true} margin="dense" >
                    <TextField value={title} onChange={(evt) => setTitle(evt.target.value)} label="Title" required={true} variant="outlined" size="small" type="text" name="title" />
                </FormControl>
                <FormControl fullWidth={true} margin="dense">
                    <TextField value={recreationSteps} onChange={(evt) => setRecreationSteps(evt.target.value)} label="Recreation Steps" required={true} variant="outlined" multiline rows={3} name="recreationSteps"  />
                </FormControl>
                <FormControl fullWidth={true} margin="dense">
                    <Input key={fileInputKey} onChange={(event) => setFormFiles(event.target.files)} inputProps={{multiple: true, accept: "image/*"}} type="file" name="bugFiles" />
                    <RenderBugFiles />
                </FormControl>                    
                <FormControl fullWidth={true} margin="dense">
                    <TextField value={aboutSoftware} onChange={(evt) => setAboutSoftware(evt.target.value)} label="About Software" required={true} variant="outlined" multiline rows={3} name="aboutSoftware"  />
                </FormControl>
                <FormControl fullWidth={true} margin="dense">
                    <InputLabel htmlFor="bug-software-type-id">Software Type</InputLabel>
                    <Select value={bugSoftwareTypeId} onChange={(evt) => setBugSoftwareTypeId(evt.target.value)} label="Software Type" required={true} labelId="bug-software-type-id" name="bugSoftwareTypeId"  >
                        {bugSoftwareTypes.map(item => <MenuItem key={item.id} value={item.id}>{item.label}</MenuItem>)}
                    </Select>
                </FormControl>
                <FormControl fullWidth={true} margin="dense">
                    <TextField value={comment} onChange={(evt) => setComment(evt.target.value)} label="Comment" variant="outlined" multiline rows={3} name="comment" />
                </FormControl>                    
                <FormControl fullWidth={true} margin="dense" style={{textAlign: "right"}}>
                    {button}                        
                </FormControl>
            </form>
            <Notification vertical="bottom" horizontal="left"/>            
        </React.Fragment>
    )
}

function RenderBugFiles() {    
    let gridList = [];
    const existingBugFiles = useSelector(state => state.bugReducer.existingBugFiles)
    const dispatch = useDispatch()

    if(existingBugFiles && existingBugFiles.length > 0) {
        gridList = existingBugFiles
                        .filter(bugFile => bugFile.filePath)
                        .map(bugFile => (
                            <GridListTile key={bugFile.bugFileId}>
                                <img src={bugFile.filePath} alt="[Resource Not Found]" />
                                <GridListTileBar actionIcon={<IconButton color="secondary"><DeleteIcon onClick={() => dispatch({ type: REMOVE_EXISTING_BUG_FILE, payload: bugFile.bugFileId }) } /></IconButton>}/>
                            </GridListTile> 
                            ));        
    }
    return (
        <GridList>
            {gridList}
        </GridList>
    )
}