import { SET_AUTHENTICATION_TOKEN, SET_ALERT_POPUP } from './types.js';

export const defaultState = {
    googleClientId: "738814746760-gs1hst1dds900ohd7su159bpa1orqa9c.apps.googleusercontent.com",
    authenticationToken: null,
    alertPopup: { open: false }
}

export const globalReducers = (state = defaultState, action) => {
    switch(action.type) {
        case SET_AUTHENTICATION_TOKEN:
            state = { ...state, authenticationToken: action.payload }
        break;
        case SET_ALERT_POPUP:
            state = { ...state, alertPopup: action.payload }
        break;
        default:
            state = state;
        break;
    }
    return state;
}