import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Dialog, DialogActions, DialogContent, DialogTitle, Button, Grid } from '@material-ui/core';
import { SET_ALERT_POPUP } from '../../store/types.js';
import ErrorIcon from '@material-ui/icons/Error';

const ResultType = {
    Success: 1,
    Error: 2,
    Warning: 3
}

export default function AlertPopup(props){

    const [open, setOpen] = useState(false);

    const { alertPopup } = useSelector(state => state.globalReducers)

    const dispatch = useDispatch();

    useEffect(() => {
        setOpen(alertPopup.open);
    }, [alertPopup]);

    const handleClose = () =>{
        dispatch({ type: SET_ALERT_POPUP, payload: { open: false }});
        if(props.closeCallback) props.closeCallback();
    }

    const handleOk = () => {
        dispatch({ type: SET_ALERT_POPUP, payload: { open: false }});
        if(props.okCallback) props.okCallback();
    }

    const cancelButton = (props.showCancel) ? <Button onClick={handleClose} color="primary"> Cancel </Button> :  '';

    const content = Array.isArray(alertPopup.content)? <ul>{alertPopup.content.map((item, index) => <li key={index}>{item}</li>)}</ul> : <p>{alertPopup.content}</p>

    return (

        <Dialog
            open={open}
            onClose={handleClose}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <DialogTitle id="alert-dialog-title">
                <Grid container direction="row" alignItems="center">
                    <ErrorIcon style={{fill: (alertPopup.type === ResultType.Success) ? "green" : (alertPopup.type === ResultType.Warning) ? "orange" : "red"}} /> &nbsp;{alertPopup.title}
                </Grid>
            </DialogTitle>
            <DialogContent>
                {content}
            </DialogContent>
            <DialogActions>
                {cancelButton}
                <Button onClick={handleOk} color="primary" autoFocus>
                    Ok
                </Button>
            </DialogActions>
        </Dialog>
    );
}