import { SET_BUGS, SET_BUGS_COUNT, SET_BUG, RESET_BUG, SET_BUG_FILES, SET_NOTIFICATION } from './types.js'
import {SET_ALERT_POPUP } from '../../types';
import Axios from 'axios';

export const createBug = (formFiles) => {
    return async(dispatch, getState) => {        
        const  { bug } = getState().bugReducer;
        const formData = getFormData(bug, formFiles);

        const { data: { data: isCreated, message, messages } } = await Axios.post("/bugs", formData);
        if(isCreated) {
            dispatch({type: SET_NOTIFICATION, payload: { open: true, message, type: 'success' } })
            dispatch(getBugs())            
            dispatch({type: RESET_BUG })            
        }
        else{
            dispatch({ type: SET_ALERT_POPUP, payload: { 
                open: true, 
                title: 'Errors', 
                content: message || messages
            }})
        }
    }
}

export const getBugs = () => {
    return async(dispatch, getState) => {

        const  { pageSize, pageNumber } = getState().bugReducer;
        const gridOperation = {
            paging: { pageSize, pageNumber: pageNumber + 1 }
        };

        const { data: { data: { item1: bugs, item2: totalCount } } } = await Axios.post("bugs/all", gridOperation);
        dispatch({type: SET_BUGS, payload: bugs});
        dispatch({type: SET_BUGS_COUNT, payload: totalCount});
    }
}

export const getBug = (bugId) => {
    return async(dispatch) => {
        const { data: { data: { item1: bug, item2: existingBugFiles  } } } = await Axios.get(`bugs/${bugId}`);
        dispatch({type: SET_BUG, payload: bug })
        dispatch({type: SET_BUG_FILES, payload: existingBugFiles })
    }
}

export const updateBug = (formFiles) => {
    return async(dispatch, getState) => {
        const { bug, existingBugFiles } = getState().bugReducer;
        const formData = getFormData(bug, formFiles, existingBugFiles);

        const { data: { data: isUpdated, message, messages } } = await Axios.put(`/bugs/${bug.bugId}`, formData);
        if(isUpdated){
            dispatch({type: SET_NOTIFICATION, payload: { open: true, message, type: 'success' } })
            dispatch(getBugs())            
            dispatch({type: RESET_BUG })
        }
        else{
            dispatch({ type: SET_ALERT_POPUP, payload: { 
                open: true, 
                title: 'Errors', 
                content: message || messages
            }})
        }
    }
}

export const deleteBug = (bugId) => {
    return async(dispatch) => {
       const { data: { data: isDeleted, message  } } = await Axios.delete(`/bugs/${bugId}`);
       if(isDeleted)
            dispatch(getBugs()) 
    }
}

function getFormData(bug, formFiles, existingBugFiles) {    
    const formData = new FormData();        
    for(let property in bug)
        formData.append(`bug[${property}]`, (bug[property]) ? bug[property] : "")

    if(formFiles) {
        for(let i = 0; i < formFiles.length; i++)
            formData.append(`formFiles`, formFiles[i]);
    }

    if(existingBugFiles) {
        for(let j = 0; j < existingBugFiles.length; j++)
            for(let property in existingBugFiles[j])
                formData.append(`bugFiles[${j}].${property}`, existingBugFiles[j][property]);
    }

    return formData;
}