import React from 'react';
import { connect } from 'react-redux';
import { SET_NOTIFICATION}  from '../../store/modules/bugs/types.js';
import { Snackbar } from '@material-ui/core';
import MuiAlert from '@material-ui/lab/Alert';

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

class Notification extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            vertical: this.props.vertical,
            horizontal: this.props.horizontal,
            open: false,
            message: '',
            duration: 6000,
            severity: null
        }
    }

    componentDidUpdate(prevsProps) {
        if(this.props.notificationOptions.open != prevsProps.notificationOptions.open)
            this.setState((state, props) => ({ open: props.notificationOptions.open, message: props.notificationOptions.message }))

    }

    render() {
        return (
            <Snackbar 
                anchorOrigin={{ vertical: this.state.vertical, horizontal: this.state.horizontal }} 
                open={this.state.open}
                autoHideDuration={this.state.duration}
                onClose={this.props.closeNotification}>
                    <Alert severity={this.state.type}>
                        {this.state.message}
                    </Alert>
            </Snackbar>
        )
    }
}

function mapStateToProps(state) {
    return {
        notificationOptions: state.bugReducer.notification
    }
}

function mapDispatchToProps(dispatch){
    return {
        closeNotification: () => dispatch({ type: SET_NOTIFICATION, payload: { open: false, message: "", type: null }})
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(Notification);