import { SET_BUGS, SET_BUGS_COUNT, SET_BUGS_PAGE_NUMBER, SET_BUG, SET_BUG_FILES, RESET_BUG, REMOVE_EXISTING_BUG_FILE, SET_NOTIFICATION}  from './types.js';
import { defaultState } from './index.js';

export const bugReducer = (state = defaultState, action) => {
    switch(action.type){
        case SET_BUGS:
            state = { ...state, bugList: action.payload }
        break;        
        case SET_BUGS_PAGE_NUMBER:
            state = { ...state, pageNumber: action.payload }
        break;        
        case SET_BUGS_COUNT:
            state = { ...state, totalCount: action.payload }
        break;        
        case SET_BUG:
            state = { ...state, bug: { ...state.bug, ...action.payload } }
            break;
        case SET_BUG_FILES:
            state = { ...state, existingBugFiles: action.payload }
        break;
        case RESET_BUG:
            state = { ...state, bug: defaultState.bug, existingBugFiles: [], isReset: !state.isReset }
        break;
        case REMOVE_EXISTING_BUG_FILE:
            state = { ...state, existingBugFiles: state.existingBugFiles.filter(item => item.bugFileId !== action.payload) }
        break;
        case SET_NOTIFICATION:
            state = { ...state, notification: action.payload }
        break;
        default:
            state = { ...state };
        break;
    }
    return state;
}